import { Component, OnInit } from '@angular/core';
import { NavParams, ModalController } from '@ionic/angular';
import * as moment from 'moment';

@Component({
    selector: 'app-book',
    templateUrl: './book.page.html',
    styleUrls: ['./book.page.scss'],
})
export class BookPage implements OnInit {
    
    
    public profile:any;
    public booking:any;
    
    constructor(private navParams: NavParams, public modalController: ModalController) { 
    
        let profile = this.navParams.get('profile');
        this.profile = Object.assign({}, profile );     
        
        
        this.booking = {booked_user_id:this.profile.user_id};
    
    }

    ngOnInit() {
    }
    
    
    public confirmBooking(){
        
        if (this.booking.date_required){
            let localeOffset = -(new Date().getTimezoneOffset());
            this.booking.date_required = moment(this.booking.date_required).add(localeOffset, 'm').format("YYYY-MM-DD HH:mm:ss");   
        }        
        
        this.modalController.dismiss({booking:this.booking});
    }

    public dismiss(){
        this.modalController.dismiss();
    }  
        
    
    public formatDate(date){
       return moment(date).format("DD/MM/YYYY hh:mm a");
    }      

}
