import { Component, OnInit } from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {AuthenticationService} from '../../services/authentication/authentication.service';
import {ProfileService} from '../../services/profile/profile.service';
import {GigService} from '../../services/gig/gig.service';
import {EditProfilePage} from '../edit-profile/edit-profile.page';
import {AddPortfolioItemPage} from '../add-portfolio-item/add-portfolio-item.page';
import {EditPortfolioItemPage} from '../edit-portfolio-item/edit-portfolio-item.page';
import {BookPage} from '../book/book.page';
import { ToastController, ModalController,AlertController } from '@ionic/angular';
import * as moment from 'moment';
import { InAppBrowser } from '@ionic-native/in-app-browser/ngx';

@Component({
    selector: 'app-profile',
    templateUrl: './profile.page.html',
    styleUrls: ['./profile.page.scss'],
})
export class ProfilePage implements OnInit {
    
    public profile:any;
    public loading:boolean;
    public activeTab:string;
    public user:any;
    public rating:Array<number>;
    
    constructor(private authenticationService:AuthenticationService, private route:ActivatedRoute, 
        private profileService: ProfileService, public modalController: ModalController, 
        private alertCtrl: AlertController, private iab: InAppBrowser, private gigService:GigService) { 
    
        let userId = this.route.snapshot.params['id'];
        this.profile = {user_id:userId, standalone:userId ? false : true, name:"Loading..."};
        this.loading = true;
        this.activeTab = "profile";
        this.user = {};
    
    
    }

    ngOnInit() {
    }
    
    ionViewDidEnter(){
        this.authenticationService.getUserData().subscribe((data) => {

            this.user = data;
            
            console.log(data);
            
            if (!this.profile.standalone && this.user.id !== this.profile.user_id){
                this.getProfile();     
            }
            else{               
                
                Object.assign(this.profile, data["profile"]);
                
                            
                this.profile.instruments = this.profile.instruments_played ? this.profile.instruments_played.split(", ") : [];
                this.profile.portfolio = this.profile.portfolio ? JSON.parse(this.profile.portfolio) : [];                 
                
                
                console.log(data);
                this.loading = false;
                
                this.profileService.getProfile(this.profile.user_id).then((data) => {
                   this.profile["reviews"] = data["reviews"];
                   this.profile["rating"] = data["rating"];
                   this.profile["profile_categories"] = data["profile_categories"];
                   this.profile.instruments = data["instruments_played"] ? data["instruments_played"].split(", ") : [];
                   this.profile.portfolio = data["portfolio"] ? JSON.parse(data["portfolio"]) : [];   
                   
                   console.log(this.profile);
                                   
                   this.calculateRating();
                    
                });
            }
            
        });          
    }
    
    public getProfile(){
        this.profileService.getProfile(this.profile.user_id).then((data) => {
            this.profile = data;
            this.profile.instruments = this.profile.instruments_played ? this.profile.instruments_played.split(", ") : [];
            this.profile.portfolio = this.profile.portfolio ? JSON.parse(this.profile.portfolio) : [];              
                   console.log(this.profile);
            this.calculateRating();
            this.loading = false;
        }).catch(() => {
            this.loading = false
        })
    }    
    
    public async editProfile(){
        let profile = Object.assign({}, this.profile);
        profile.email = this.user.email;
        const modal = await this.modalController.create({
          component: EditProfilePage,
          componentProps: {profile:profile}
        });
        
        modal.onDidDismiss()
            .then((data:any) => {
                if (data && data.data){
                    let profile = data.data.profile;
                    delete profile.instruments;
                    delete profile.portfolio;
                    delete profile.reviews;
                    
                    Object.assign(this.profile, profile);
                    this.profile.instruments = this.profile.instruments_played ? this.profile.instruments_played.split(", ") : [];
                    this.profile.standalone = true;
                    
                    this.profileService.updateProfile(profile).then(() => {
                        this.authenticationService.retreiveUserData().then(() => {
                            
                      
                            
                        })
                    })            

                } 
            });        
        
        
        
        await modal.present();         
    } 
    
    
    public async bookUser(){
        let profile = Object.assign({}, this.profile);
        profile.email = this.user.email;
        const modal = await this.modalController.create({
          component: BookPage,
          componentProps: {profile:profile}
        });
        
        modal.onDidDismiss()
            .then((data:any) => {
                if (data && data.data){
                    console.log(data);           
                    this.gigService.bookUser(data.data.booking).then((data) => {
                        let alert = this.alertCtrl.create({
                            header: 'Booking Requested',
                            message: 'Your booking has successfully been requested. The musician will receive a notification to accept the booking. Once accepted, you will be booked for this date and time.',
                            buttons: [
                                {
                                    text: 'Dismiss',
                                    role: 'cancel',
                                    handler: data => {
                                    }
                                }  
                            ]
                        }).then((el) => {
                            el.present();
                        });                           
                    }).catch(() => {
                        let alert = this.alertCtrl.create({
                            header: 'Error',
                            message: 'There was an error trying to make your booking. Please try again.',
                            buttons: [
                                {
                                    text: 'Dismiss',
                                    role: 'cancel',
                                    handler: data => {
                                    }
                                }  
                            ]
                        }).then((el) => {
                            el.present();
                        });                             
                    })
                } 
            });        
        
        
        
        await modal.present();         
    }    
    
    
    public async editPortfolioItem(item,index){
        let itemCopy = Object.assign({}, item);
        const modal = await this.modalController.create({
          component: EditPortfolioItemPage,
          componentProps: {item:itemCopy}
        });
        
        modal.onDidDismiss()
            .then((data:any) => {
                if (data && data.data){
                    let item = data.data.item;
                    console.log(data);
                    Object.assign(this.profile.portfolio[index], item);
                    let profile = {portfolio: JSON.stringify(this.profile.portfolio), user_id:this.profile.user_id};
                    
                    this.profileService.updateProfile(profile).then(() => {
                        this.authenticationService.retreiveUserData().then(() => {
                                  
                            
                            
                        })
                    })            

                } 
            });        
        
        
        
        await modal.present();         
    }     
    
    public async addPortfolioItem(){
        const modal = await this.modalController.create({
          component: AddPortfolioItemPage,
          componentProps: {}
        });
        
        modal.onDidDismiss()
            .then((data:any) => {
                if (data && data.data){
                    let item = data.data.item;
                    console.log(this.profile);
                    this.profile.portfolio.push(item);
                    let profile = {portfolio: JSON.stringify(this.profile.portfolio), user_id:this.profile.user_id};
                   
                    
                    this.profileService.updateProfile(profile).then(() => {
                        this.authenticationService.retreiveUserData().then(() => {})
                    })            

                } 
            });        
        
        
        
        await modal.present();         
    }         
     
    
    public viewVideo(url){
         this.iab.create(url, '_system');
    }
    
    public calculateRating(){
        this.rating = [];
        for (let i = 0; i < this.profile.rating; i++){
            this.rating.push(i);
        }
    }
    
    public formatDate(date){
        return moment(date).format("DD/MM/YYYY");
    }      
    

    
    
}
