import { Component, OnInit, NgZone } from '@angular/core';
import { Router, RouterEvent, NavigationEnd } from '@angular/router';
import { LoadingController,AlertController,ModalController,NavController,NavParams } from '@ionic/angular';
import { Camera, CameraOptions } from '@ionic-native/camera/ngx';
import {FileTransfer, FileUploadOptions, FileTransferObject} from '@ionic-native/file-transfer/ngx/';

import {environment} from '../../../environments/environment';

import {AuthenticationService} from '../../services/authentication/authentication.service';
import {CityService} from '../../services/city/city.service';

import {MusicianService} from '../../services/musician/musician.service';


import * as moment from 'moment';

@Component({
  selector: 'app-edit-profile',
  templateUrl: './edit-profile.page.html',
  styleUrls: ['./edit-profile.page.scss'],
})
export class EditProfilePage implements OnInit {
    
    public profile:any;
    public instruments:Array<any>;
    public categories:Array<any>;
    
    
    constructor(private router: Router,private transfer: FileTransfer, 
        private camera: Camera, public loadingController: LoadingController, public alertController: AlertController,
        public modalController: ModalController, private navController:NavController,
        private authenticationService:AuthenticationService,private navParams: NavParams, private cityService:CityService,
        private musicianService:MusicianService, private ngZone: NgZone) { 
    
        let profile = this.navParams.get('profile');
        this.profile = Object.assign({}, profile ); 
        
        
        this.profile.instruments = this.profile.instruments_played ? this.profile.instruments_played.split(", ") : [];
        console.log(this.profile);
        

        this.profile.categories = [];
        
        
        this.categories = [];
        this.musicianService.getCategories("").then((data:Array<any>) => {
            this.categories = data;

            let categories = [];
            for (let category of this.profile.profile_categories){
                categories.push(category.category_id);
            }
            this.profile.categories = categories;

        });
        
        
        this.instruments = ["Vocals","Guitar", "Bass", "Piano", "Banjo", "Cello", "Clarinet", "Drums", "Flute", "Trumpet", "Harmonica", "Organ", "Recorder", "Saxophone", "Trombone", "Ukelele", "Tuba", "Viola", "Violin", "Xylophone"].sort();
            
    
    }

    ngOnInit() {
    }

    public dismiss(){
        this.modalController.dismiss();
    }  
    
        
    public changeImage(){
        let cameraOptions = {
            sourceType: this.camera.PictureSourceType.PHOTOLIBRARY,
            destinationType: this.camera.DestinationType.FILE_URI,  
            encodingType: this.camera.EncodingType.JPEG,    
            quality: 25
        }

        this.camera.getPicture(cameraOptions)
            .then((image) => {
                let loading;
                this.loadingController.create({
                  message: 'Your image is being uploaded. Please wait...',
                  duration: 20000
                }).then((el) => {
                    loading = el;
                    el.present();
                });           

                const fileTransfer: FileTransferObject = this.transfer.create();                
                
                let options: FileUploadOptions = {
                    fileKey:"media_file",
                    fileName: Math.random().toString(36).substring(2),
                    params: {},
                    mimeType : "image/jpeg"
                }
             
                
                fileTransfer.upload(image, encodeURI(environment.apiUrl + "/upload"), options).then((data) => {

                    if (data.response){
                        let response = JSON.parse(data.response);
                        this.profile.avatar = response.file;  
                    }     
                    loading.dismiss();           
                }, (err) => {
                    this.alertController.create({
                        header: "Error",
                        subHeader: "There was a problem uploading your image",
                        message: JSON.stringify(err),
                        buttons: [
                            {
                                text: 'Dismiss',
                                role: 'cancel'
                            }
                        ]
                    }).then((el) => {
                        el.present(); 
                    });
                    
                    loading.dismiss();
                                       
                })
      
                
            }, 
            err => {
                //console.log(err)
            });   
    } 
    
    
    public save(){
        if (this.profile.dob){
            this.profile.dob = moment(this.profile.dob).format("YYYY-MM-DD HH:mm:ss");
        }
        
        console.log(this.profile.instruments_played);
        console.log(this.profile.instruments);
        if (this.profile.instruments && this.profile.instruments.length){
            console.log(this.profile);
            this.profile.instruments_played = this.profile.instruments.join(", ");
        }

        this.profile.profile_categories = [];
        for (let category of this.profile.categories){
            this.profile.profile_categories.push({user_id:this.profile.user_id, category_id:category});
        }

        
        this.modalController.dismiss({profile:this.profile});
    }    


}
